// HOC例子
import React from "react";
import ReactDom from "react-dom";
import PropTypes from "prop-types";
import { message, Icon } from "antd";
import { ZcodeHighlight, ZpageWraperHOC, ZerodMainContext, Zlayout, zTool, ZfullLayer, Zviewer } from "zerod";
import cssClass from "./style.scss";
import ClipboardJS from "clipboard";

//兼容ie10的dataset属性
function getDataset(element) {
	var obj = {};
	if (element.dataset) {
		return element.dataset;
	} else {
		for (var i = 0; i < element.attributes.length; i++) {
			var key = element.attributes[i].nodeName;
			if (/^data-\w+$/.test(key)) {
				//判断是否以data-开头的属性名
				var value = element.attributes[i].nodeValue; //值
				var keyName = key.match(/^data-(\w+)/)[1]; //键名
				obj[keyName] = value; //对象添加属性
			}
		}
	}
	return obj;
}

function findParentElBy(el, condition) {
	let parEl = el.parentElement;
	while (parEl && condition(parEl)) {
		parEl = parEl.parentElement;
	}
	return parEl;
}

const ZpageWraper = ZpageWraperHOC();
const copyicon = `<svg viewBox="64 64 896 896" class="z-code-copy" data-icon="copy" width="1em" height="1em" fill="currentColor" aria-hidden="true">
<title>复制代码内容</title><path d="M832 64H296c-4.4 0-8 3.6-8 8v56c0 4.4 3.6 8 8 8h496v688c0 4.4 3.6 8 8 8h56c4.4 0 8-3.6 8-8V96c0-17.7-14.3-32-32-32zM704 192H192c-17.7 0-32 14.3-32 32v530.7c0 8.5 3.4 16.6 9.4 22.6l173.3 173.3c2.2 2.2 4.7 4 7.4 5.5v1.9h4.2c3.5 1.3 7.2 2 11 2H704c17.7 0 32-14.3 32-32V224c0-17.7-14.3-32-32-32zM350 856.2L263.9 770H350v86.2zM664 888H414V746c0-22.1-17.9-40-40-40H232V264h432v624z"></path></svg>`;

function addCopyBtnOfCode(el) {
	const codeBox = document.createElement("div");
	codeBox.style.position = "relative";
	codeBox.innerHTML = `<div class="z-code-actions-box z-flex"><div class="z-code-actions-btn">${copyicon}</div></div>`;
	el.parentElement.replaceChild(codeBox, el);
	codeBox.appendChild(el);
}

function AmdDocHOC(doc, renders) {
	class Doc extends React.PureComponent {
		renderEls = [];
		state = {
			navs: [],
			closeModaled: true,
			scrollWrapEl: null,
			putout: false,
			viewerUrls: [],
		};
		//根据标题创建右栏导航
		setNavs = () => {
			const _navs = [];
			const _docTitles = this.codeRef.current.boxEl.children;
			// console.dir(_docTitles);

			Array.prototype.slice.call(_docTitles).forEach((el) => {
				if (["h1", "h2", "h3", "h4", "h5", "h6"].includes(el.localName)) {
					_navs.push({
						name: el.innerText,
						el,
						active: -this.scrollInstance.scroll.y == el.offsetTop - 24,
					});
				}
			});
			if (_navs.length) {
				this.setState(
					{
						navs: _navs,
					},
					() => {
						this.bindScrollEvent();
					},
				);
			}
		};
		scrollEnd = () => {
			const y = -this.scrollInstance.scroll.y;
			this.state.navs.forEach((item) => {
				item.active = false;
			});
			const len = this.state.navs.length;
			for (let i = 0; i < len; i++) {
				const item = this.state.navs[i];
				if (y >= item.el.offsetTop - 120) {
					if (i < len - 1) {
						const nextItem = this.state.navs[i + 1];
						if (nextItem && y < nextItem.el.offsetTop - 120) {
							item.active = true;
							break;
						}
					} else {
						item.active = true;
						break;
					}
				}
			}
			this.setState({
				navs: [...this.state.navs],
			});
		};
		bindScrollEvent = () => {
			this.scrollInstance.scroll.on("scrollEnd", this.scrollEnd);
		};
		navScrollTo = (item) => {
			this.scrollInstance.scroll.scrollTo(0, -(item.el.offsetTop - 24), 200);
		};
		setLi = (ul) => {
			Array.prototype.slice.call(ul).forEach((ul) => {
				if (ul.children) {
					Array.prototype.slice.call(ul.children).forEach((li) => {
						li.className += " rounded-list";
						li.innerHTML = `<a href="javascript:void(0)">${li.innerHTML}</a>`;

						const aul = li.querySelectorAll("a>ul");
						const aol = li.querySelectorAll("a>ol");
						if (aul) {
							this.setLi(aul);
						}
						if (aol) {
							this.setLi(aol);
						}
					});
				}
			});
		};
		//添加复制代码的小图标
		addCopyIcon = () => {
			const _pres = this.mdEl.querySelectorAll("pre");
			Array.prototype.slice.call(_pres).forEach((el) => {
				addCopyBtnOfCode(el);
			});
			this.clipboard = new ClipboardJS(".z-code-copy", {
				target: function(trigger) {
					const parent = findParentElBy(trigger, (parEl) => {
						return parEl.className && !parEl.className.includes("z-demo-code-btn") && !parEl.className.includes("z-code-actions-box");
					});
					return parent.nextElementSibling.querySelector("code");
				},
			});
			this.clipboard.on("success", function(e) {
				if (e.action == "copy") {
					message.success("已复制到粘贴板");
				}
				// console.info("Action:", e.action);
				// console.info("Text:", e.text);
				// console.info("Trigger:", e.trigger);

				e.clearSelection();
			});
			this.clipboard.on("error", function(e) {
				console.error("Action-error:", e.action);
				// console.error("Trigger-error:", e.trigger);
			});
		};
		//给a标签添加target属性
		addTargetOfA = () => {
			const _doca = this.mdEl.querySelectorAll("a");
			const menuData = this.props.getSideMenuData();
			Array.prototype.slice.call(_doca).forEach((a) => {
				const pathname = a.pathname;
				const finded = zTool.itemsFromTree({
					tree: menuData,
					keyObj: { id: "path", children: "children" },
					sourceItem: { path: pathname },
				});
				if (finded) {
					a.setAttribute("href", "javascript:void(0)");
					a.onclick = () => {
						this.props.$router.history.push(pathname);
					};
				} else {
					a.setAttribute("target", finded ? "_self" : "_blank");
				}
			});
		};
		//给li标签添加clasName
		listHtml = () => {
			const _docUl = this.mdEl.querySelectorAll("div>ul");
			const _docOl = this.mdEl.querySelectorAll("div>ol");
			this.setLi(_docUl);
			this.setLi(_docOl);
		};

		//把img从li里面抽离出来
		separateImgFromLi = () => {
			const _docimg = this.mdEl.querySelectorAll("img");
			const viewerUrls = [];
			Array.prototype.slice.call(_docimg).forEach((img) => {
				if (img.className == "z-open-code") return;
				let _parUl = img;
				while (_parUl && _parUl.localName !== "ul" && _parUl.localName !== "ol") {
					_parUl = _parUl.parentElement;
				}
				_parUl && _parUl.parentElement && _parUl.parentElement.insertBefore(img, _parUl.nextElementSibling);
				img.onclick = () => {
					this.openViewer(img.currentSrc);
				};
				viewerUrls.push(img.currentSrc);
			});
			this.setState({
				viewerUrls,
			});
		};
		//创建demo展示区域，折叠代码
		createDemoArea = () => {
			const _allDemoBox = this.mdEl.querySelectorAll(".z-demo-box");
			Array.prototype.slice.call(_allDemoBox).forEach((el) => {
				const _dataset = getDataset(el);
				if (renders && typeof renders[_dataset.render] == "function") {
					let pre = el.nextElementSibling;
					pre = pre && pre.localName == "pre" ? pre : null;
					const Content = renders[_dataset.render](this.props);
					this.renderEls.push(el);
					ReactDom.render(Content, el, () => {
						const _div = document.createElement("div");
						_div.className = "z-demo-footer";
						const _divcodebtn = document.createElement("div");
						_divcodebtn.className = "z-demo-code-btn z-flex-space-between";
						const _divcode = document.createElement("div");
						_divcode.className = "z-demo-code";
						const _img = document.createElement("img");
						const actions = document.createElement("div");
						actions.className = "z-flex";
						actions.innerHTML = copyicon;
						_img.className = "z-open-code";
						_img.addEventListener(
							"click",
							() => {
								_img.src = _img.open ? "https://gw.alipayobjects.com/zos/rmsportal/wSAkBuJFbdxsosKKpqyq.svg" : "https://gw.alipayobjects.com/zos/rmsportal/OpROPHYqWmrMDBFMZtKF.svg";
								_divcode.style.height = _img.open ? "0px" : "auto";
								_img.open = !_img.open;
							},
							false,
						);
						_img.src = "https://gw.alipayobjects.com/zos/rmsportal/wSAkBuJFbdxsosKKpqyq.svg";
						const _title = document.createElement("span");
						_title.innerText = _dataset.title ? _dataset.title : "";
						actions.appendChild(_img);
						_divcodebtn.appendChild(_title);
						pre && _divcodebtn.appendChild(actions);
						_div.appendChild(_divcodebtn);
						pre && _divcode.appendChild(pre);
						_div.appendChild(_divcode);
						el.appendChild(_div);
					});
				}
			});
		};
		componentDidMount() {
			const saveData = this.props.getTemporaryStorage();
			if (zTool.dataType.isObject(saveData)) {
				saveData.sliderValue&&this.setFontSize(saveData.sliderValue);
				this.props.setTemporaryStorage({
					...saveData,
					doc: this,
				});
			} else {
				this.props.setTemporaryStorage({
					doc: this,
				});
			}
			this.addTargetOfA();
			this.createDemoArea();
			this.listHtml();
			this.addCopyIcon();
			this.separateImgFromLi();
			this.mdEl.addEventListener(
				"click",
				(e) => {
					if (typeof e.target.className === "string" && e.target.className.includes("z-open-modal-btn")) {
						setTimeout(() => {
							this.unmounted = false;
							if (typeof e.target._render == "function") {
								const contents = e.target._render();
								if (contents.windowType != "layer") {
									this.props.showRightModal({
										show: true,
										modal: e.target._modal ? e.target._modal : "mainModal",
										content: contents,
										scroll: e.target._scroll,
										onTransitionend: (show) => {
											if (!this.unmounted)
												this.setState({
													closeModaled: !show,
												});
										},
										// wrapperEl:document.body
									});
								} else {
									const { header, content } = contents;
									this.layerRef.current.methods.setHeader(header);
									this.layerRef.current.methods.setContent(content);
									this.layerRef.current.layerMethods.showLayer(true, null, true)();
								}
							}
						}, 10);
					}
				},
				false,
			);
			this.mdEl.addEventListener("click", (e) => {
				if (typeof e.target.className === "string" && e.target.className.includes("z-show-loading-btn")) {
					setTimeout(() => {
						if (e.target._modal == "mainRoute") {
							this.props.showRouteLoading(true);
							setTimeout(() => {
								this.props.showRouteLoading(false);
							}, 2000);
						} else {
							const modal = e.target._modal ? e.target._modal : "mainModal";
							this.unmounted = false;
							this.props.showRightModal({
								show: true,
								modal,
								content: null,
								scroll: true,
								onTransitionend: (show) => {
									if (!this.unmounted)
										this.setState({
											closeModaled: !show,
										});
								},
							});
							this.props.showModalLoading(true, modal);
							setTimeout(() => {
								this.props.showModalLoading(false, modal);
							}, 2000);
						}
					}, 10);
				} else if (typeof e.target.className === "string" && e.target.className.includes("z-history-href")) {
					const path = e.target.dataset.path;
					if (typeof path === "string") {
						this.props.$router.history.push(path);
					}
				}
			});
			this.scrollInstance = this.props.getScrollInstance("mainRoute");
			setTimeout(() => {
				if (this.unmounted) return;
				this.setRightNav();
				this.setNavs();
			}, 200);
		}
		unmounted = false;
		componentWillUnmount() {
			this.unmounted = true;
			this.clipboard && this.clipboard.destroy();
			this.renderEls.forEach((el) => {
				ReactDom.unmountComponentAtNode(el);
			});
			if (this.scrollInstance && this.scrollInstance.scroll) this.scrollInstance.scroll.off("scrollEnd", this.scrollEnd);
		}
		layerRef = React.createRef();
		codeRef = React.createRef();
		viewerRef = React.createRef();
		openViewer = (url) => {
			this.viewerRef.current && this.viewerRef.current.viewer.show(true);
			this.viewerRef.current && this.viewerRef.current.viewer.view(this.state.viewerUrls.indexOf(url));
		};
		setRightNav = () => {
			const w = this.props.getScrollAreaWrapperEl("mainRoute");
			w.methods.setScrollAreaClassName(cssClass["z-center-content"]);
			this.setState(
				{
					scrollWrapEl: w.wrapperEl,
				},
				() => {
					if (this.navEl && this.navContentEl) {
						const { BuildScroll } = zTool;
						this.navScroollInstance = new BuildScroll(this.navEl, { scrollbars: "custom" });
						zTool.listenDivSizeChange(this.navEl, this.navScroollInstance.refresh);
						zTool.listenDivSizeChange(this.navContentEl, this.navScroollInstance.refresh);
					}
				},
			);
		};
		putout = () => {
			const w = this.props.getScrollAreaWrapperEl("mainRoute");
			w.methods.setScrollAreaClassName(this.state.putout ? cssClass["z-center-content"] : "");
			this.setState({
				putout: !this.state.putout,
			});
		};
		setFontSize = (size) => {
			this.mdEl && (this.mdEl.style.fontSize = size ? size : "");
		};
		render() {
			return (
				<Zlayout.Template>
					<ZpageWraper pageHeader={{ show: false }} hasBodyPadding={false}>
						<div className={`z-panel ${cssClass["z-md-doc"]}`} ref={(el) => (this.mdEl = el)}>
							<div className={`z-panel-body z-md-doc-body`}>
								<ZcodeHighlight mode="html" ref={this.codeRef}>
									{doc}
								</ZcodeHighlight>
							</div>
						</div>
					</ZpageWraper>
					{this.state.scrollWrapEl
						? ReactDom.createPortal(
								<div className={`${cssClass["z-nav-box"]} ${this.state.putout ? cssClass["out"] : ""}`}>
									<div className={cssClass["z-nav-putout"]} onClick={this.putout}>
										<Icon type={`double-${this.state.putout ? "left" : "right"}`} />
									</div>
									<div style={{ overflow: "hidden", height: "100%", position: "relative", zIndex: 1, backgroundColor: "white" }} ref={(el) => (this.navEl = el)}>
										<div ref={(el) => (this.navContentEl = el)}>
											<section className="z-padding-top-10 z-padding-bottom-10">
												{this.state.navs.map((item, i) => {
													return (
														<div
															title={item.name}
															className={`${cssClass["z-nav"]} ${item.active ? cssClass["active"] : ""}`}
															key={i}
															onClick={() => {
																this.navScrollTo(item);
															}}
														>
															{item.name}
														</div>
													);
												})}
											</section>
										</div>
									</div>
								</div>,
								this.state.scrollWrapEl,
						  )
						: null}
					<Layer ref={this.layerRef} />
					<div className={cssClass["z-hide-viewer"]}>
						<Zviewer urls={this.state.viewerUrls} ref={this.viewerRef} />
					</div>
				</Zlayout.Template>
			);
		}
	}
	return ZerodMainContext.setConsumer(Doc);
}
class Layer extends React.PureComponent {
	methods = {
		setHeader: (c) => {
			this.setState({
				layerHeader: c,
			});
		},
		setContent: (c) => {
			this.setState({
				layerContent: c,
			});
		},
	};
	state = {
		layerHeader: null,
		layerContent: null,
	};
	getMethods = (m) => {
		this.layerMethods = m;
	};
	render() {
		const { layerHeader, layerContent } = this.state;
		return (
			<ZfullLayer header={layerHeader} exportMethods={this.getMethods}>
				{layerContent}
			</ZfullLayer>
		);
	}
}

export default {
	name: "AmdDocHOC",
	HOC: AmdDocHOC,
};
